@font-face {
    font-family: 'Circe';
    src: url('../../assets/fonts/circe/circe_bold.eot');
    src: url('../../assets/fonts/circe/circe_bold.woff2') format('woff2');
    src: url('../../assets/fonts/circe/circe_bold.ttf') format('truetype');
    src: url('../../assets/fonts/circe/circe_bold.svg#Circe-Bold') format('svg');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Circe';
    src: url('../../assets/fonts/circe/circe.eot');
    src: url('../../assets/fonts/circe/circe.eot?#iefix')
        format('embedded-opentype');
    src: url('../../assets/fonts/circe/circe.woff2') format('woff2');
    src: url('../../assets/fonts/circe/circe.ttf') format('truetype');
    src: url('../../assets/fonts/circe/circe.svg#circe') format('svg');
}

@font-face {
    font-family: 'Poppins';
    src: local('Poppins Regular'), local('Poppins-Regular');
    src: url('../../assets/fonts/Poppins/Poppins-Regular.woff2') format('woff2');
    src: url('../../assets/fonts/Poppins/Poppins-Regular.woff') format('woff');
    src: url('../../assets/fonts/Poppins/Poppins-Regular.ttf')
        format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Poppins';
    src: url('../../assets/fonts/Poppins/Poppins-Bold.eot');
    src: local('Poppins Bold'), local('Poppins-Bold'),
        url('../../assets/fonts/Poppins/Poppins-Bold.eot?#iefix')
            format('embedded-opentype'),
        url('../../assets/fonts/Poppins/Poppins-Bold.woff2') format('woff2'),
        url('../../assets/fonts/Poppins/Poppins-Bold.woff') format('woff'),
        url('../../assets/fonts/Poppins/Poppins-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Segoeui';
    src: url('../../assets/fonts/sego/segoeui.ttf') format('truetype');
    src: url('../../assets/fonts/sego/segoeuib.ttf') format('truetype');
}
